#root .calc_app > .calc_layout > button.b_active {
  background-color: red;
  color: #000;
}

.calc_app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  border-radius: 10px;
  padding: 20px;

  .calc_layout {
    display: grid;
    gap: 3px;
    padding: 5px;
    grid-template-columns: repeat(4, 1fr);
    border: 1px grey solid;
    width: 100%;

    .calc_dispay {
      grid-column-start: 1;
      grid-column-end: 5;

      margin: 3px auto;
      width: 98%;

      div {
        border-radius: 5px;
        width: 100%;
        height: 50px;
        border: 1px solid grey;
        background-color: transparent;
        font-size: 30px;
        font-weight: bold;
        text-align: right;
        color: #000;
      }
    }

    button.calc_buttons {
      margin: 3px auto !important;
      width: 90%;
      height: 100%;
      border: 1px solid grey;
      background-color: #f5f5f5;
      font-size: 30px;
      font-weight: bold;
      text-align: center;
      color: #000;
      border-radius: 10px;
      cursor: pointer;
    }

    // button.b_active {
    //   background-color: red;
    //   color: #000;
    // }

    button.button_white {
      background-color: white;
      border: 1px solid grey;
      border-radius: 5px;
      padding: 10px;
      font-size: 20px;
      font-weight: bold;
      color: #000;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &:hover {
        background-color: gray;
      }
      &:active {
        background-color: red;
      }
    }

    button.button_gray {
      background-color: lightgrey;
      border: 1px solid grey;
      border-radius: 5px;
      padding: 10px;
      font-size: 20px;
      font-weight: bold;
      color: #000;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &:hover {
        background-color: gray;
      }
      &:active {
        background-color: red;
      }
    }
  }
}
